import React from "react";
import { v4 as uuidv4 } from "uuid";
import { Button, Container, Form, Navbar, Table } from "react-bootstrap";

import "bootstrap/dist/css/bootstrap.min.css";
import TableRow from "./TableRow";
import GenerateYAML from "./GenerateYAML";
import ModelMembers from "./ModelMembers";

const initialList = [
  // {
  //   id: "a",
  //   name: "test1",
  //   model: true,
  //   controller: false,
  //   seeder: false,
  //   migration: false,
  //   isComplete: false,
  //   members: []
  // },
  // {
  //   id: "b",
  //   name: "test2",
  //   model: true,
  //   controller: false,
  //   seeder: false,
  //   migration: false,
  //   isComplete: false,
  //   members: []
  // }
];

const App = () => {
  const [list, setList] = React.useState(initialList);
  const [name, setName] = React.useState("");

  function handleChange(event) {
    setName(event.target.value);
  }

  function handleAdd() {
    const newList = list.concat({
      id: uuidv4(),
      name,
      model: true,
      controller: true,
      seeder: true,
      migration: true,
      isComplete: true,
      members: []
    });

    setList(newList);

    setName("");
  }

  function handleToggleComplete(id, key, value) {
    const newList = list.map((item) => {
      if (item.id === id) {
        const updatedItem = {
          ...item,
          [key]: value
        };

        return updatedItem;
      }

      return item;
    });

    setList(newList);
  }

  function handleRemove(id) {
    const newList = list.filter((item) => item.id !== id);

    setList(newList);
  }

  return (
    <div>
      <Container>
        <Navbar expand="lg" variant="light" bg="light">
          <Container>
            <Navbar.Brand href="#">LARAFAST</Navbar.Brand>
          </Container>
        </Navbar>
        <hr />
        <Form.Label>Enter a table name</Form.Label>
        <AddItem name={name} onChange={handleChange} onAdd={handleAdd} />
        <hr />
        {/* <List
        list={list}
        onToggleComplete={handleToggleComplete}
        onRemove={handleRemove}
      /> */}
        {list.length > 0 && (
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>#</th>
                <th>Name</th>
                <th>Model</th>
                <th>Controller</th>
                <th>Seeder</th>
                {/* <th>Migration</th> */}
                <th>Delete</th>
              </tr>
            </thead>
            <TableRow
              list={list}
              onToggleComplete={handleToggleComplete}
              onRemove={handleRemove}
            />
          </Table>
        )}
        {list.map((item, ixx) => (
          <ModelMembers
            key={ixx}
            updateMembers={handleToggleComplete}
            {...item}
          />
        ))}
        <hr />
        <GenerateYAML list={list} />
      </Container>
    </div>
  );
};

const AddItem = ({ name, onChange, onAdd }) => (
  <div>
    <input type="text" value={name} onChange={onChange} /> &nbsp;
    <Button onClick={onAdd}>Add model</Button>
  </div>
);

const List = ({ list, onToggleComplete, onRemove }) => (
  <ul>
    {list.map((item) => (
      <li key={item.id}>
        {item.id} {item.name} {item.isComplete + ""} {item.model + ""}{" "}
        {item.controller + ""} {item.seeder + ""} {item.migration + ""}
        {
          <Button onClick={() => onToggleComplete(item.id)}>
            {item.isComplete ? "Undo" : "Done"}
          </Button>
        }
        {/* <Button onClick={() => onRemove(item.id)}>Remove</Button> */}
      </li>
    ))}
  </ul>
);
export default App;

