import React from "react";
import { Alert, Button, Card, FormControl } from "react-bootstrap";

export default function GenerateYAML(props) {
  const [generatedCode, setGeneratedCode] = React.useState("");

  function downloadTxtFile() {
    const element = document.createElement("a");
    const file = new Blob([generatedCode], {
      type: "text/plain"
    });
    element.href = URL.createObjectURL(file);
    element.download = "draft.yaml";
    document.body.appendChild(element); // Required for this to work in FireFox
    element.click();
  }

  function copyYAML() {
    var copyText = generatedCode;
    navigator.clipboard.writeText(copyText);
  }

  function addTab(count) {
    let spaces = "";
    for (let i = 0; i < count; i++) {
      spaces += "    ";
    }
    return spaces;
  }

  function println(content) {
    return content + "\n";
  }

  function updateYAML() {
    let genCode = "";
    let seedersText = "";
    let seedersCount = 0;
    let controllerCount = 0;
    if (props.list.length > 0) {
      genCode = println("models:");
    }
    for (let i = 0; i < props.list.length; i++) {
      genCode += println(addTab(1) + props.list[i].name + ":");
      //add members
      let tMembers = props.list[i].members;
      console.log(tMembers.length);
      for (let j = 0; j < tMembers.length; j++) {
        genCode += println(
          addTab(2) +
            tMembers[j].colName +
            ": " +
            tMembers[j].dataType +
            " " +
            tMembers[j].options
        );
      }

      if (props.list[i].seeder !== false) {
        if (seedersCount === 0) {
          seedersText = props.list[i].name;
        } else {
          seedersText += ", " + props.list[i].name;
        }
        seedersCount++;
      }

      if (props.list[i].controller !== false) {
        controllerCount++;
      }
    }

    if (seedersCount > 0) {
      genCode += println("seeders: " + seedersText);
    }

    if (controllerCount > 0) {
      genCode += println("controllers:");
      for (let k = 0; k < props.list.length; k++) {
        genCode += println(addTab(1) + props.list[k].name + ":");
        genCode += println(addTab(2) + "resource");
      }
    }
    setGeneratedCode(genCode);
  }

  return (
    <Card>
      <Card.Header>
        Generated YAML File for{" "}
        <a href="https://blueprint.laravelshift.com/">Laravel Blueprint</a>
      </Card.Header>
      <Card.Body>
        <Card.Text>
          <Alert variant="info">
            <b>Pro tip</b>: You can edit the below content manually, for further
            customization.
          </Alert>
          <FormControl
            as="textarea"
            value={generatedCode}
            onChange={(e) => setGeneratedCode(e.target.value)}
            rows={10}
          />
        </Card.Text>
        <Button variant="primary" onClick={updateYAML}>
          Generate YAML
        </Button>
        {/* &nbsp;
        <Button variant="primary" onClick={copyYAML}>
          Copy YAML
        </Button>
        &nbsp;
        <Button variant="primary" onClick={downloadTxtFile}>
          Download YAML
        </Button> */}
      </Card.Body>
    </Card>
  );
}
