import React from "react";
import {
  Badge,
  Button,
  Card,
  Form,
  FormControl,
  OverlayTrigger,
  Table,
  Tooltip
} from "react-bootstrap";
import { v4 as uuidv4 } from "uuid";

const supportedDataTypes = [
  "bigIncrements",
  "bigInteger",
  "binary",
  "boolean",
  "char",
  "dateTimeTz",
  "dateTime",
  "date",
  "decimal",
  "double",
  "enum",
  "float",
  "foreignId",
  "foreignIdFor",
  "foreignUuid",
  "geometryCollection",
  "geometry",
  "id",
  "increments",
  "integer",
  "ipAddress",
  "json",
  "jsonb",
  "lineString",
  "longText",
  "macAddress",
  "mediumIncrements",
  "mediumInteger",
  "mediumText",
  "morphs",
  "multiLineString",
  "multiPoint",
  "multiPolygon",
  "nullableMorphs",
  "nullableTimestamps",
  "nullableUuidMorphs",
  "point",
  "polygon",
  "rememberToken",
  "set",
  "smallIncrements",
  "smallInteger",
  "softDeletesTz",
  "softDeletes",
  "string",
  "string:5",
  "string:10",
  "string:50",
  "string:100",
  "string:500",
  "text",
  "timeTz",
  "time",
  "timestampTz",
  "timestamp",
  "timestampsTz",
  "timestamps",
  "tinyIncrements",
  "tinyInteger",
  "tinyText",
  "unsignedBigInteger",
  "unsignedDecimal",
  "unsignedInteger",
  "unsignedMediumInteger",
  "unsignedSmallInteger",
  "unsignedTinyInteger",
  "uuidMorphs",
  "uuid",
  "year"
];

const initialList = [
  // {
  //   id: "a",
  //   colName: "member1",
  //   dataType: "id",
  //   options: "nullable"
  // },
  // {
  //   id: "b",
  //   colName: "member2",
  //   dataType: "integer",
  //   options: "default"
  // }
];

export default function ModelMembers(props) {
  const [columnList, setColumnList] = React.useState(initialList);
  const [colName, setColName] = React.useState("");

  function updateParent() {
    console.log("updates sent to parent");
    props.updateMembers(props.id, "members", columnList);
  }

  function handleChange(event) {
    setColName(event.target.value);
    updateParent();
  }

  function addColumn() {
    console.log(colName);
    const newList = columnList.concat({
      id: uuidv4(),
      colName: colName,
      dataType: "",
      options: ""
    });

    setColumnList(newList);
    setColName("");
    updateParent();
  }

  function removeColumn(id) {
    const newList = columnList.filter((item) => item.id !== id);

    setColumnList(newList);
    updateParent();
  }

  function updateDataType(id, key, value) {
    const newList = columnList.map((item) => {
      if (item.id === id) {
        const updatedItem = {
          ...item,
          [key]: value
        };

        return updatedItem;
      }

      return item;
    });

    setColumnList(newList);
    updateParent();
  }

  return (
    <div>
      <Card>
        <Card.Header>
          <b>{props.name}</b>
          <AddItem
            id={props.id}
            name={colName}
            onChange={handleChange}
            onAdd={addColumn}
          />
          <OverlayTrigger
            key="right"
            placement="right"
            overlay={
              <Tooltip id="overlay-example">
                Updates are delayed by one user action for table column members
                in CodeGen.
              </Tooltip>
            }
          >
            <Button variant="secondary">Info on bug.</Button>
          </OverlayTrigger>
        </Card.Header>
        <Card.Body>
          {columnList.length > 0 && (
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Column name</th>
                  <th>Data type</th>
                  <th>Options</th>
                  <th>Delete</th>
                </tr>
              </thead>
              <tbody>
                {columnList.map((item, index) => (
                  <tr key={item.id}>
                    <td>{index + 1}</td>
                    <td>{item.colName}</td>
                    <td>
                      <Form.Select
                        onChange={(e) =>
                          updateDataType(item.id, "dataType", e.target.value)
                        }
                        value={item.dataType}
                      >
                        {supportedDataTypes.map((option, ix) => (
                          <option key={ix} value={option}>
                            {option}
                          </option>
                        ))}
                      </Form.Select>
                    </td>
                    <td>
                      <FormControl
                        as="textarea"
                        value={item.options}
                        onChange={(e) =>
                          updateDataType(item.id, "options", e.target.value)
                        }
                      />
                    </td>
                    <td>
                      <Button onClick={() => removeColumn(item.id)}>
                        {"Remove "}
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          )}
        </Card.Body>
      </Card>
    </div>
  );
}

const AddItem = ({ name, onChange, onAdd }) => (
  <div>
    <input type="text" value={name} onChange={onChange} /> &nbsp;
    <Button onClick={onAdd}>Add column</Button>
  </div>
);
